*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'SFRegular', 'Helvetica';
}

body{
    background-image: url('/src/img/BG.png');
    background-repeat: no-repeat;
    background-position: top center;

    -webkit-touch-callout: none;
    -webkit-user-select: none; 
     -khtml-user-select: none; 
       -moz-user-select: none; 
        -ms-user-select: none; 
            user-select: none; 
                                 
}

.container{
    max-width: 700px;
    max-height: 800px;
    width: 100%;
    margin: auto;
    margin-top: 100px;
    padding: 20px;

    background: rgba(32, 32, 32, 0.6);
    backdrop-filter: blur(10px);

    overflow-y: scroll;
}

.containerTimers{
    border: 2px solid #FAE600;
}

.containerTimers + .containerTimers{
    margin-top: 40px;
}

.containerTitle{
    background: #FAE600;
    padding: 0 10px;
    margin-left: 20px;

    font-size: 30px;
    line-height: 80%;

    color: rgba(32, 32, 32, 0.9);
}

.containerTitle, .containerTitle{
    display: flex;
    justify-content: space-between;
}

.wrapperTimer{
    padding: 0 20px;
}

.containerTitleAvailable{
    display: flex;
    justify-content: flex-end;

    padding: 0 20px 10px 0;

    color: rgb(240, 240, 240)
}

.titleAvailable{
    margin-right: 10px;

    color: #FAE600;
}

.availableTimerText{
    display: flex;
    margin-top: 5px;
    justify-content: center;

    font-size: 20px;

    color: #444444
}

.addTimerBtn{
    width: 100%;
    background: #FAE600;
    border: 4px solid #FAE600;

    width: 100%;
    height: 32px;

    background-image: url('/src/img/iconAddTimer.png');
    background-repeat: no-repeat;
    background-position: center center;

    cursor: pointer;
}

/*! timer.jsx !*/
.containerTimer{
    display: flex;
    margin: 8px 0;
    
    border-bottom: 1px solid #FAE600;

    background: rgba(63, 63, 63, 0.6);
    color: rgb(240, 240, 240)
}

.conteinerTimerStart{
    display: flex;
    max-width: 280px;
    width: 100%;
}

.completedTimerBtn, .removeTimerBtn, .startTimerBtn, .cancelTimerBtn{
    background: #FAE600;
    background-repeat: no-repeat;
    background-position: center center;

    width: 50px;

    border: none;
    cursor: pointer;
}

.completedTimerBtn{
    background-image: url('/src/img/iconComplete.png');
}

.removeTimerBtn{
    background-image: url('/src/img/iconRemove.png');
}

.cancelTimerBtn{
    background-image: url('/src/img/iconCancel.png');
}

.startTimerBtn{
    background-image: url('/src/img/iconStart.png');
}

.timer{
    padding: 20px 20px 20px 35px;
}

.time{
    display: flex;
    justify-content: flex-start;
    font-size: 38px;
}

.containerTimerEnd{
    display: flex;
    justify-content: flex-end;
    width: 100%;
}

.completedTimerText{
    color: #FAE600;
}

.containerTimerSelect{
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100%;
}

.timerSelect{
    background: #202020;
    border: 1px solid #FAE600;

    align-self: center;

    width: 100px;
    height: 40px;

    font-size: 16px;
    outline: none;
    
    color: rgb(240, 240, 240)
}

/*! scrollstyle !*/
::-webkit-scrollbar {
    width: 0.5vw;
}
  
::-webkit-scrollbar-track {
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: #FAE600; 
    border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
    background: #FAE600; 
}